@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
.create {
  width: 300px;
  padding: 15px 15px 25px;
  border-radius: 7px;
  background-color: #fff;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create h3,
.create h3 b {
  font-size: 21px;
  font-weight: 600;
  line-height: 45px;
  border-bottom: solid 2px #fff;
  padding-bottom: 3px;
  text-transform: uppercase;
}
.create h3 b {
  border-bottom-color: #D62246;
}
.create input {
  width: 90%;
  height: 40px;
  border-bottom: solid 1px #cecece;
  padding: 5px 15px;
  margin: 7px 0;
  font-weight: 600;
}
.create button {
  width: 90%;
  height: 40px;
  margin-top: 20px;
  background-color: #D62246;
  color: #dbdbdb;
  border-radius: 5px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  transition: -webkit-filter 0.3s;
  transition: filter 0.3s;
  transition: filter 0.3s, -webkit-filter 0.3s;

  display: flex;
  align-items: center;
  justify-content: center;
}
.create button i {
  font-size: 21px;
  margin-right: 5px;
}
.create button:hover {
  -webkit-filter: brightness(115%);
          filter: brightness(115%);
}

@media (max-width: 800px) {
  .create {
    width: 90%;
  }
  .create button {
    height: 50px;
  }
}

.item {
  width: 400px;
  height: 130px;
  margin: 15px 10px;
  padding: 15px;
  border-radius: 5px;
  border-right: solid 2px #9b9b9b;
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
  align-items: center;  
}
.item img {
  width: 100px;
  border-radius: 50%;
  margin-right: 15px;
}
.item .data {
  z-index: 20;
  display: flex;
  flex-direction: column;
  max-width: calc(100% - 115px);
}
.item .data h3 {
  font-size: 17px;
  font-weight: 600;
  word-wrap: break-word;
}
.item .data h4 {
  font-size: 14px;
  font-weight: 600;
  color: #D62246;
  margin-top: 10px;
}
.item .data a {
  text-decoration: none;
  color: #636363;
}
.item .action-buttons {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.item .action-buttons button {
  width: 35px;
  height: 35px;
  margin: -5px 5px 0;
  color: #fff;
  background-color: #c2c2c2;
  border-radius: 10px;
  transition: -webkit-filter 0.3s;
  transition: filter 0.3s;
  transition: filter 0.3s, -webkit-filter 0.3s;
  cursor: pointer;
}
.item .action-buttons button i {
  font-size: 20px;
}
.item .action-buttons button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%)
}
@media (max-width: 800px) {
  .item {
    height: auto;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    padding: 35px 0;
    margin: 20px 0;
    border-right-width: 0;
  }
  .item .data {
    width: 100%;
    margin-top: 12px;
    align-items: center;
  }  
  .item .action-buttons {
    justify-content: center;
  }
  .item .action-buttons button {
    margin: 0 5px -50px;
  }
}
.list {
  width: 900px;
  padding: 15px;

  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
}
.list .filter {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.filter .change {
  display: flex;
  justify-content: center;
  align-items: center;
}
.filter .change button {
  width: 35px;
  height: 35px;
  background-color: #D62246;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: -webkit-filter 0.3s;
  transition: filter 0.3s;
  transition: filter 0.3s, -webkit-filter 0.3s;
}
.filter .change button:nth-child(1) {
  margin-right: 10px;
}
.filter .change button:nth-child(3) {
  margin-left: 10px;
}
.filter .change button:hover {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}
.filter .change label,
.filter .change label b {
  font-size: 18px;
}
.filter .change.limit button {
  background-color: #cecece;
  color: #fff;
}
.filter .change input {
  height: 40px;
  width: 240px;
  padding: 5px 10px;
  background: none;
  border-bottom: solid 2px #cecece;
  border-radius: 0 0 5px 5px;
  font-weight: 600;
}
@media (max-width: 800px) {
  .list {
    width: 100%;
    justify-content: center;
    margin-top: 20px;
  }  
  .list .filter {
    padding: 25px 0;
    /* flex-direction: column; */
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: #fff;
  }
  .filter .change.limit {
    width: 50%;
    order: 1;
  }
  .filter .change.page {
    width: 50%;
    order: 2;
  }
  .filter .change.search {
    order: 3;
  }
  .filter .change label,
  .filter .change label b {
    font-size: 15px;
  }
  
  .filter .change.search input {
    width: 300px;
    margin-top: 15px;
  }
}
.home {
  width: 1300px;
  padding: 40px 20px;
  
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 800px) {
  .home {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: none;
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100vh;
  overflow: auto;

  display: flex;
  justify-content: center;
  align-items: flex-start;
}

body {
  background-color: #f1f1f1;
}

.shadow {
  box-shadow: 0px 3px 7px 0px rgba(26, 26, 26, 0.20);
}

@media (max-width: 800px) {
  .hide_in_mobile {
    display: none;
  }
}
